import React from 'react'
import {useState} from 'react'


export default function TextForm(props) {
    const [text, setText] = useState('');
    //text=new text; //Wrong way
    //setText(new text); // correct way

    const uppr = () =>{
        let newText = text.toUpperCase();
        setText(newText);
        props.showAlert("Text has been Converted to Upper case","success");
        
    };

    const handleup =(event) =>{
        console.log("onchange");
        setText(event.target.value)
    };

    const lower = () => {
        let text2 = text.toLowerCase();
        setText(text2);
        props.showAlert("Text has been Converted to Lower case","success");
    };

    const speak = () => {
      let msg = new SpeechSynthesisUtterance();
      msg.text = text;
      window.speechSynthesis.speak(msg);
      props.showAlert(null,"success");
      
    }
    
    const clear = () => {
      let ntext = '';
      setText(ntext);
      props.showAlert("Text has been cleared","success");
    }
  return (
    <>
    <div className="container" style={{color:props.mode==='dark'?'white':'black'}}>
        <h1>{props.heading}</h1>
        <div className="mb-3">
          <textarea className="form-control my-3" id="mybox" rows="8" value={text} onChange={handleup} style={{backgroundColor:props.mode==='dark'?'grey':'white',color:props.mode==='dark'?'white':'black'}}> </textarea>
        </div>
          <button type="button mb-8" className="btn btn-primary" onClick={uppr}  >Convert to Upper case</button>
          <button type="button" className="btn btn-primary mx-2" onClick={lower}  >Convert to Lower case</button>
          <button type="submit" onClick={speak} className="btn btn-warning mx-2 my-2">Speak</button>
          <button type="button" className="btn btn-danger mx-2" onClick={clear} onChange={handleup}>Clear</button>
    </div>
        <div style={{color:props.mode==='dark'?'white':'black'}}>
         <h2> Your Text preview</h2>
         <p><b>{text.split(" ").length} words and {text.length} characters</b></p>
         <p><b>{0.08 * text.split(" ").length}min to read </b></p>
         <h3>Preview</h3>
         <p>{text.length>0?text:"Enter Somenthing in TextBox to preview it here"}</p>
         
        </div>
    </>
  )
}
